import React, { Component } from 'react';

export default class Cell extends Component{
    constructor(props){
        super(props);

        this.state = ({
            isMouseDown: false,
        });
    }

    clickHandle(x,y){
        const tool = this.props.tools.currentItem;
        if( tool === 'hand') {
            let newArray = this.props.map.source;
            newArray[x][y] = this.props.blocks.selectedBlock;
            this.props.mapActions.changedMapSource(newArray);
        }
        if(tool === 'delete'){
            let newArray = this.props.map.source;
            newArray[x][y] = '';
            this.props.mapActions.changedMapSource(newArray);
        }
        if(tool === 'select'){
            let item = this.props.map.selectedItem;
            item[x][y] = 1;
            this.props.mapActions.changedMapSelect(item);
        }
    }

    selectHandle(x,y){
        const tool = this.props.tools.currentItem;
        if(this.props.map.select && tool === 'hand'){
            let newArray = this.props.map.source;
            newArray[x][y] = this.props.blocks.selectedBlock;
            this.props.mapActions.changedMapSource(newArray);
        }
        if(this.props.map.select && tool === 'delete'){
            let newArray = this.props.map.source;
            newArray[x][y] = '';
            this.props.mapActions.changedMapSource(newArray);
        }
    }

    moveStartHandle(){
        const { selectCells } = this.props.mapActions;
        selectCells(true);
    }

    unMoveHandle(){
        if(this.props.map.select) {
            const {selectCells} = this.props.mapActions;
            selectCells(false);
        }
    }

    render(){
        let num = this.props.serialNumber;
        let temp = 'cell ';

        temp += this.props.map.mapping[num] ? this.props.map.mapping[num] : '';
        if(this.props.map.selectedItem[this.props.x][this.props.y] === 1)
            temp += 'selected ';

        return (
            <div
                className={temp}
                onClick={this.clickHandle.bind(this, this.props.x, this.props.y)}
                onMouseDown={this.moveStartHandle.bind(this)}
                onMouseMove={this.selectHandle.bind(this, this.props.x, this.props.y)}
                onMouseUp={this.unMoveHandle.bind(this)}
            >

            </div>
        );
    }
}